<style scoped>
.wrap {
  width: 100%;
  height: 100%;
}

.tale-list {
  flex: 1;
}
</style>

<template>
  <section class="wrap flex-column">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <div class="operation">
      <div class="operation-btns">
          <el-button type="primary" @click="commodityAdd"></el-button>
          <!-- <el-button type="danger"  @click="commodityDeleteAll"></el-button> -->
      </div>
      <el-form :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="searchForm.title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- / -->

    <div class="tale-list">
      <el-table :data="commodities" border stripe style="min-width: 900px;" @selection-change="handleSelectionChange">
        <!-- <el-table-column type="selection"></el-table-column> -->
        <el-table-column prop="id" label="ID" sortable></el-table-column>
        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column prop="description" label="" show-overflow-tooltip></el-table-column>
        <el-table-column label="" prop="sales" sortable>
          <template slot-scope="scope">
            <span>{{scope.row.sales}} </span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="text" @click="checkStandard(scope.row.id)"></el-button>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="text" @click="checkProduct(scope.row)"></el-button>
          </template>
        </el-table-column>
        <el-table-column prop="created_at" label=""></el-table-column>
        <el-table-column label="" width="200">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="commodityEdit(scope.$index, scope.row)"></el-button>
            <el-button size="small" type="danger"  @click="commodityDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <div class="pages">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>
    <!-- / -->
  </section>
</template>

<script>
export default {
  data() {
    return {
      //
      waittingData: [],

      // form
      searchForm: {
        title: ""
      },

      currentPage: 1,
      count: 0,

      //
      commodities: []
    };
  },

  created() {
    this.$api.getCommodityInfo("", res => {
      this.commodities = res.data.data;
      this.count = res.data.count;
    });
  },

  methods: {
    //
    commodityAdd() {
      this.$router.push({ name: "commodityAdd", params: { commodity: null } });
    },

    //
    // commodityDeleteAll() {
    //   const ids = this.waittingData;
    //   if (ids.length) {
    //     console.log(ids);
    //     this.$confirm(", ?", "", {
    //       confirmButtonText: "",
    //       cancelButtonText: "",
    //       type: "warning"
    //     })
    //       .then(() => {
    //         this.$message({
    //           type: "success",
    //           message: "!"
    //         });
    //       })
    //       .catch(() => {
    //         this.$message({
    //           type: "info",
    //           message: ""
    //         });
    //       });
    //   } else {
    //     this.$message({
    //       message: "",
    //       type: "warning"
    //     });
    //   }
    // },

    /**
     * 
     */
    checkStandard(id) {
      sessionStorage.commodity_id = id;
      this.$router.push({ name: "commodityStandardsList" });
    },

    /**
     * 
     */
    checkProduct(row) {
      sessionStorage.commodity_id = row.id;
      sessionStorage.commodity_title = row.title;
      this.$router.push({ name: "commoditySpecificList" });
    },

    /**
     * 
     * @param {array object} selected 
     */
    handleSelectionChange(selected) {
      let temp = [];
      for (let it of selected) {
        temp.push(it.id);
      }
      this.waittingData = temp;
    },

    //
    handleSearch() {
      this.$api.getCommodityInfo({ title: this.searchForm.title }, res => {
        this.commodities = res.data.data;
        this.count = res.data.count;
      });
    },

    /**
     * 
     * @param {number} index 
     * @param {object} row 
     */
    commodityEdit(index, row) {
      this.$router.push({
        name: "commodityAdd",
        params: { commodity: row }
      });
    },

    /**
     * 
     * @param {number} index 
     * @param {object} row 
     */
    commodityDelete(index, row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$api.deleteCommodityInfo(row.id, res => {
            this.commodities.splice(index, 1);
            this.count--;
            this.$message({
              type: "success",
              message: ""
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    /**
     * 
     * @param {number} page 
     * */
    handleCurrentChange(page) {}
  }
};
</script>




