<style scoped>
.wrap {
  width: 100%;
  height: 100%;
}

.tale-list {
  flex: 1;
}

.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>

<template>
  <section class="wrap flex-column">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <div class="operation">
      <el-form :inline="true" :model="searchForm">
        <el-form-item label="">
          <el-select v-model="searchForm.area" placeholder="">
            <el-option v-for="sheng in shengs" :key="sheng.id" :label="sheng.fullname" :value="sheng.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="searchForm.state" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- / -->

    <div class="tale-list">
      <el-table :data="masterLists" border stripe style="min-width: 900px;">
        <el-table-column prop="id" label="ID" sortable></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="id_card" label=""></el-table-column>
        <el-table-column prop="phone" label=""></el-table-column>
        <el-table-column prop="good_at" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="created_at" label=""></el-table-column>
        <el-table-column prop="state" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.state == 0" class="normal"></span>
            <span v-else-if="scope.row.state == 1" class="success"></span>
            <span v-else class="warning"></span>
          </template>
        </el-table-column>
        <el-table-column prop="state" label="">
          <template slot-scope="scope">
            <el-button v-if="scope.row.state == 0" size="small" type="primary" @click="passMaster(scope.$index, scope.row.id)"></el-button>
            <el-button v-if="scope.row.state == 0" size="small" type="danger" @click="rejectMaster(scope.$index, scope.row.id)"></el-button>
            <span v-else></span>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <div class="pages">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>
    <!-- / -->
  </section>
</template>

<script>
export default {
  data() {
    return {
      searchForm: {
        state: ""
      },

      shengs: "",

      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },

      masterLists: [],
      count: 0,
      currentPage: 1
    };
  },

  created() {
    this.$api.getApplies({ state: 0 }, res => {
      this.masterLists = res.data.data;
      this.count = res.data.count;
    });

    this.$api.getDistrict((err, res) => {
      this.shengs = res.result[0];
    });
  },

  methods: {
    //
    search() {
      this.$api.getApplies(this.searchForm, res => {
        this.masterLists = res.data.data;
        this.count = res.data.count;
      });
    },

    //
    passMaster(index, id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$api.postApply(id, { state: 1 }, res => {
            this.masterLists.splice(index, 1);
            this.count--;
            this.$message({
              type: "success",
              message: ""
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    //
    rejectMaster(index, id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$api.postApply(id, { state: 2 }, res => {
            this.masterLists.splice(index, 1);
            this.count--;
            this.$message({
              type: "warning",
              message: ""
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    //
    handleCurrentChange(page) {
      this.$api.getApplies(
        { state: this.searchForm.state, page: page },
        res => {
          this.masterLists = res.data.data;
        }
      );
    }
  }
};
</script>




