<style scoped>
.wrap {
  width: 100%;
  height: 100%;
}

.tale-list {
  flex: 1;
}
</style>

<template>
  <section class="wrap flex-column">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <div class="operation">
      <el-form :inline="true" :model="searchForm">
        <el-form-item label="">
          <el-select v-model="searchForm.status" placeholder="" @change="onSubmit">
            <el-option label="" value="2"></el-option>
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <!-- / -->

    <div class="tale-list">
      <el-table :data="reservations" border stripe style="min-width: 900px;">
        <el-table-column prop="id" label="ID" sortable></el-table-column>
        <el-table-column prop="created_at" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="number" label=""></el-table-column>
        <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="worker" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.state == 1">{{scope.row.worker}}</span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column prop="state" label="" sortable>
          <template slot-scope="scope">
            <span v-if="scope.row.state == 0" class="warning"></span>
            <span v-else class="success"></span>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <div class="pages">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>
    <!-- / -->
  </section>
</template>

<script>
export default {
  data() {
    return {
      // form
      searchForm: {
        status: "2"
      },

      count: 0,

      statusText: {
        0: "",
        1: ""
      },
      currentPage: 1,

      //
      reservations: []
    };
  },

  created() {
    this.$api.getReservations("", res => {
      this.reservations = res.data.data;
      this.count = res.data.count;
    });
  },

  methods: {
    /**
     * @param {number} page 
     * */
    handleCurrentChange(page) {
      const state = this.searchForm.status;
      let searchData = {
        page: page
      };
      if (state != 2) {
        searchData.state = state;
      }
      this.$api.getReservations(searchData, res => {
        this.reservations = res.data.data;
      });
    },

    //
    onSubmit(e) {
      const state = e;
      let searchData = {};
      if (state != 2) {
        searchData.state = state;
      }
      this.$api.getReservations(searchData, res => {
        this.reservations = res.data.data;
        this.count = res.data.count;
      });
    }
  }
};
</script>




