<style scoped>
.img-item {
  /* width: 250px; */
  height: 250px;
  border-radius: 5px;
  margin: 20px;
  position: relative;
}

.img-item img {
  /* width: 100%; */
  height: 100%;
  border-radius: 5px;
}

.img-del {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.4);
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
}

.img-del i {
  font-size: 40px;
  cursor: pointer;
  color: #888;
  margin: 0 10px;
}
.img-item:hover .img-del {
  display: flex;
}

.no-pic {
  font-size: 20px;
  text-align: center;
  flex: 1;
}
</style>

<template>
  <section class="wrap">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <div class="operation">
      <div class="operation-btns">
          <el-button type="primary" @click="productdAddImg"></el-button>
          <!-- <el-button type="danger"  @click="productDeleteAll"></el-button> -->
      </div>
    </div>
    <!-- / -->

    <div class="img-list">
      <div class="flex-row" v-if="images.length">
        <div class="img-item" v-for="(img, index) in images" :key="img.id">
          <img :src="img.url" alt="">
          <div class="img-del">
            <i class="el-icon-view" @click="preImg(img)"></i>
            <i class="el-icon-delete" @click="delImg(img.id, index)"></i>
          </div>
        </div>
      </div>
      <div v-else class="no-pic"></div>
    </div>

    <el-dialog width="40%" :visible="dialogVisible" @close="closeDialog" style="text-align: center;">
      <img style="max-width: 100%;" :src="dialogImageUrl" alt="">
    </el-dialog>
  </section>
</template>

<script>
export default {
  data() {
    return {
      images: [],
      dialogImageUrl: "",
      dialogVisible: false
    };
  },

  created() {
    this.$api.getAdverts({ type: 3 }, res => {
      this.images = res.data.data;
    });
  },

  methods: {
    //
    productdAddImg() {
      const path = {
        path: "/company/images",
        type: 3,
        name: ""
      };
      this.$router.push({ name: "advertImgAdd", params: { path: path } });
    },

    //
    delImg(id, index) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$api.deleteAdvert(id, res => {
            this.images.splice(index, 1);
            this.$message({
              type: "success",
              message: "!"
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    /** 
     * 
     * @param {object} file 
    */
    preImg(img) {
      this.dialogImageUrl = img.url;
      this.dialogVisible = true;
    },

    // dialog
    closeDialog() {
      this.dialogVisible = false;
    }
  }
};
</script>



