<style scoped>
.wrap {
  width: 100%;
  height: 100%;
}

.tale-list {
  flex: 1;
}
</style>

<template>
  <section class="wrap flex-column">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/list/commodity' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <div class="operation">
      <div class="operation-btns">
          <el-button type="primary" @click="standardAdd"></el-button>
          <!-- <el-button type="danger"  @click="standardDeleteAll"></el-button> -->
      </div>
    </div>
    <!-- / -->

    <div class="tale-list">
      <el-table :data="standards" border stripe style="min-width: 900px;" @selection-change="handleSelectionChange">
        <!-- <el-table-column type="selection"></el-table-column> -->
        <el-table-column prop="id" label="ID" sortable></el-table-column>
        <el-table-column prop="commodity_title" label=""></el-table-column>
        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column prop="attrs" label="" show-overflow-tooltip>
          <template slot-scope="scope">
            <span v-for="(tag, index) in scope.row.attrs" :key="index" style="margin-right: 5px;">{{tag.title}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="created_at" label=""></el-table-column>
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="commodityEdit(scope.$index, scope.row)"></el-button>
            <el-button size="small" type="danger"  @click="standardDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <!-- <div class="pages">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div> -->
    <!-- / -->
  </section>
</template>

<script>
export default {
  data() {
    return {
      //
      waittingData: [],
      commodity_id: "",

      // form
      searchForm: {
        status: "0"
      },

      // currentPage: 1,
      // count: 0,

      //
      standards: []
    };
  },

  created() {
    const id = sessionStorage.commodity_id;
    this.$api.getStandards(id, res => {
      this.standards = res.data.data;
      this.commodity_id = id;
      // this.count = res.data.count;
    });
  },

  methods: {
    //
    standardAdd() {
      this.$router.push({ name: "commodityAddStandard" });
    },

    /**
     * 
     * @param {array object} selected 
     */
    handleSelectionChange(selected) {
      let temp = [];
      for (let it of selected) {
        temp.push(it.id);
      }
      this.waittingData = temp;
    },

    //
    handleSearch() {
      // console.log("search");
    },

    /**
     * 
     * @param {number} index 
     * @param {object} row 
     */
    commodityEdit(index, row) {
      this.$router.push({
        name: "commodityStandardChange",
        params: { standard: row }
      });
    },

    /**
     * 
     * @param {number} index 
     * @param {object} row 
     */
    standardDelete(index, row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$api.deleteStandards(row.id, res => {
            this.standards.splice(index, 1);
            this.$message({
              type: "success",
              message: ""
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    /**
     * 
     * @param {number} page 
     * */
    handleCurrentChange(page) {}
  }
};
</script>




