<style scoped>
.wrap {
  width: 100%;
  height: 100%;
}
.tale-list {
  flex: 1;
}

.input-with-select {
  width: 700px;
}
</style>

<template>
  <section class="wrap flex-column">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <div class="operation">
      <el-form :inline="true" :model="searchForm" style="margin-bottom: 20px;">
        <el-form-item label="">
          <el-select v-model="searchForm.state" placeholder="" @change="searchState">
            <el-option label="" value=""></el-option>
            <!-- <el-option label="" value="0"></el-option> -->
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-form :inline="true" :model="searchForm">
        <el-form-item label="">
          <el-input v-model="searchForm.number" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- / -->

    <div class="tale-list">
      <el-table :data="orders" border stripe style="min-width: 900px;">
        <el-table-column prop="number" label="" show-overflow-tooltip>
          <template slot-scope="scope">
            <el-button type="text" @click="checkOrder(scope.row.id)">{{scope.row.number}}</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="phone" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="price" label="">
          <template slot-scope="scope">
            <span class="price">{{scope.row.price}} </span>
          </template>
        </el-table-column>
        <el-table-column prop="created_at" label=""></el-table-column>
        <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="state" label="">
          <template slot-scope="scope">
            <span>{{stateText[scope.row.state]}}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="small" type="primary" v-if="scope.row.state == 1" @click="shipCommodity(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <div class="pages">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>
    <!-- / -->

    <el-dialog title="" :visible.sync="dialogBox" width="30%" center>
      <div class="shipForm">
        <el-form label-position="top" label-width="80px" :model="shipForm" :rules="rules" ref="shipForm">
          <el-form-item label="" prop="name">
            <el-input v-model="shipForm.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="track_number">
            <el-input v-model="shipForm.track_number"></el-input>
          </el-form-item>
          <el-form-item style="text-align:center">
            <el-button @click="dialogBox = false"> </el-button>
            <el-button type="primary"  @click="confirmShip('shipForm')"> </el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </section>
</template>

<script>
export default {
  data() {
    return {
      stateText: {
        0: "",
        1: "",
        2: "",
        3: ""
      },

      // form
      searchForm: {
        state: "",
        number: ""
      },

      shipForm: {
        name: "",
        track_number: ""
      },

      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        track_number: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      dialogBox: false,
      currentIndex: "",
      currentRow: {},

      currentPage: 1,
      count: 0,

      //
      orders: []
    };
  },

  created() {
    this.$api.getOrders("", res => {
      this.orders = res.data.data;
      this.count = res.data.count;
    });
  },

  methods: {
    //
    checkOrder(id) {
      this.$router.push({ name: "commodityOrderOne", params: { id } });
    },

    //
    searchState(e) {
      this.$api.getOrders({ state: e }, res => {
        this.orders = res.data.data;
        this.count = res.data.count;
      });
    },

    //
    handleSearch() {
      this.$api.getOrders({ number: this.searchForm.number }, res => {
        this.orders = res.data.data;
        this.count = res.data.count;
      });
    },

    /**
     * @param {number} page 
     * */
    handleCurrentChange(page) {
      const getData = Object.assign(
        { state: this.searchForm.state },
        { page: page }
      );
      this.$api.getOrders(getData, res => {
        this.orders = res.data.data;
        this.count = res.data.count;
      });
    },

    //
    shipCommodity(index, row) {
      this.dialogBox = true;
      this.currentIndex = index;
      this.currentRow = row;
    },

    confirmShip(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$api.shipOrder(this.currentRow.id, this.shipForm, res => {
            this.orders[this.currentIndex].state = 2;
            this.dialogBox = false;
            this.$message({
              type: "success",
              message: ""
            });
          });
        } else {
          return false;
        }
      });
    }
  }
};
</script>




