<style scoped>
.wrap {
  width: 100%;
  height: 100%;
}

.tale-list {
  flex: 1;
}

.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}

.search-form {
  text-align: right;
  margin-bottom: 20px;
}

.input-with-select {
  max-width: 900px;
}
</style>

<template>
  <section class="wrap flex-column">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <div class="operation">
      <div class="search-form">
        <el-input placeholder="" v-model="selectInput" class="input-with-select" @keypress.native.enter="selectSearch">
          <el-select v-model="select" slot="prepend" placeholder="">
            <el-option label="" value="name"></el-option>
            <el-option label="" value="id_card"></el-option>
          </el-select>
          <el-button slot="append" icon="el-icon-search" @click="selectSearch"></el-button>
        </el-input>
      </div>
      <el-form :inline="true" :model="searchForm">
        <el-form-item label="">
          <el-date-picker v-model="searchForm.time" type="datetimerange" :picker-options="pickerOptions" range-separator="" start-placeholder="" end-placeholder="" align="left">
        </el-date-picker>
        <el-form-item label="">
          <el-select v-model="searchForm.province" placeholder="">
            <el-option v-for="sheng in shengs" :key="sheng.id" :label="sheng.fullname" :value="sheng.fullname"></el-option>
          </el-select>
        </el-form-item>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- / -->

    <div class="tale-list">
      <el-table :data="workerLists" border stripe style="min-width: 900px;">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.apply.id_card }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.nickname }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.apply.address }}</span>
              </el-form-item>
              <el-form-item style="width: 100%;" label="">
                <span>{{ props.row.apply.good_at }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="id" label="ID" sortable></el-table-column>
        <el-table-column prop="apply.name" label=""></el-table-column>
        <el-table-column prop="apply.phone" label=""></el-table-column>
        <el-table-column prop="count" label="" sortable></el-table-column>
        <el-table-column prop="apply.city" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="apply.created_at" label=""></el-table-column>
        <el-table-column prop="enable" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.enable == 1" class="success"></span>
            <span v-else class="warning"></span>
          </template>
        </el-table-column>
        <el-table-column prop="state" label="">
          <template slot-scope="scope">
            <el-button v-if="scope.row.enable == 1" size="small" type="danger" @click="stopMaster(scope.$index, scope.row.id)"></el-button>
            <el-button v-else size="small" type="primary" @click="reuseMaster(scope.$index, scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <div class="pages">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>
    <!-- / -->
  </section>
</template>

<script>
export default {
  data() {
    return {
      searchForm: {
        time: ""
      },

      select: "name",
      selectInput: "",

      shengs: "",

      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },

      workerLists: [],
      count: 0,
      currentPage: 1,

      searchData1: {},
      searchData2: {}
    };
  },

  created() {
    this.$api.getWorkers("", res => {
      this.workerLists = res.data.data;
      this.count = res.data.count;
    });

    this.$api.getDistrict((err, res) => {
      this.shengs = res.result[0];
    });
  },

  methods: {
    //
    selectSearch() {
      let getData = {
        [this.select]: this.selectInput,
        search: 1
      };
      this.searchData1 = getData;
      this.$api.getWorkers(getData, res => {
        this.workerLists = res.data.data;
        this.count = res.data.count;
      });
    },
    //
    search() {
      const data = this.searchForm;
      let getData = {
        search: 1,
        province: data.province
      };
      if (data.time) {
        getData.start = data.time[0];
        getData.end = data.time[1];
      }
      this.searchData2 = getData;
      this.$api.getWorkers(getData, res => {
        this.workerLists = res.data.data;
        this.count = res.data.count;
      });
    },

    //
    reuseMaster(index, id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$api.changeWorker(id, res => {
            this.workerLists[index].enable = 1;
            this.$message({
              type: "success",
              message: ""
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    //
    stopMaster(index, id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$api.changeWorker(id, res => {
            this.workerLists[index].enable = 0;
            this.$message({
              type: "success",
              message: ""
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    //
    handleCurrentChange(page) {
      let getData = Object.assign({}, this.searchData1, this.searchData2, {
        page
      });
      this.$api.getWorkers(getData, res => {
        this.workerLists = res.data.data;
      });
    }
  }
};
</script>




